.App {
  font-family: -apple-system,
    BlinkMacSystemFont,
    sans-serif !important;
  font-style: normal;
  text-align: center;
}

.disclaimer-info {
  border: 1px solid gray;
  padding: 10px;
  text-align: left;
  float: right;
  font-size: 12px;
}

.info {
  width: 24px;
  height: 24px;
  float: left;
  padding: 10px 10px 10px 0;
}

.rcw-client {
  background: #0E538B !important;
  border-radius: 16px !important;
}

.rcw-client .rcw-message-text {
  color: white;
}

.rcw-message {
  font-weight: 400;
  font-size: 13px;
}

.rcw-conversation-container .rcw-header {
  background-color: white !important;
  color: black !important;
  padding: 16px 0 16px 0 !important;
  text-align: left !important;
  border-bottom: 1px solid #CDDAE5;
}

.rcw-conversation-container .rcw-header .rcw-title {
  margin: 0;
  padding: 0;
  font-weight: 600;
  font-size: 14px;
}

.rcw-conversation-container {
  width: 380px !important;
  margin-bottom: 16px !important;

  background: #FFFFFF !important;
  /* Light mode/shadowLow */

  box-shadow: 0px 4px 16px rgba(31, 39, 45, 0.12) !important;
  border-radius: 4px !important;
}

.rcw-send-icon {
  height: 18px !important;
  width: 21px !important;
}

.rcw-sender {
  border-radius: 0 !important;
}

.rcw-messages-container {
  padding-top: 0 !important;
}

code {
  background-color: darkgray;
  padding: 5px;
  margin: 10px 0;
}

.avatar {
  width: 16px !important;
  height: 16px !important;
  padding-left: 24px !important;
}

.rcw-avatar {
  width: 16px !important;
  height: 16px !important;

  padding: 8px;
  background: #FFF5F7;
}

.custom-launcher {
  width: 50px;
  height: 50px;
  left: 126px;
  top: 0px;

  align-self: flex-end;
  border: 0;
  border-color: transparent;
  border-radius: 50%;
  background: white;
  box-shadow: 0px 4px 16px rgba(31, 39, 45, 0.12);
}

.quick-button {
  background: #0E538B !important;
  border-radius: 4px !important;
  border: 0 !important;

  font-weight: 400 !important;
  font-size: 13px !important;
  line-height: 20px !important;
  color: #FFFFFF !important;
}

.quick-buttons-container {
  border-top: 1px solid #CDDAE5;
}